@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
:root{--background-color: hsl(180, 52%, 96%);--filter-table: hsl(180, 31%, 95%);--dark-greyish: hsl(180, 8%, 52%);--very-dark-greyish: hsl(180, 14%, 20%);--primary-color: hsl(180, 29%, 50%)}*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}html{font-size:15px}body{font-family:"Spartan", sans-serif;background-color:hsl(180, 52%, 96%);background-color:var(--background-color)}.jobs{max-width:70vw;margin:auto;display:flex;flex-direction:column}header{background-image:url(/job-listing/static/media/bg-header-desktop.8694f466.svg);width:100%;height:10rem;background-color:hsl(180, 29%, 50%);background-color:var(--primary-color)}.header-group{background-color:white;box-shadow:rgba(0,0,0,0.35) 0px 5px 15px;width:70vw;margin:-60px auto 0 auto;display:flex;align-items:center;flex-direction:row;justify-content:space-between;padding:30px;box-sizing:border-box}.header-group ul{list-style:none;display:flex;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;flex-wrap:wrap;grid-row-gap:10px;row-gap:10px}.header-group ul li{display:flex;align-items:center;padding:0 0 0 10px}@media screen and (max-width: 400px){.header-group ul li{font-size:0.8rem}.header-group ul li img{width:10px}}.header-group ul button{margin-left:7px;background-color:hsl(180, 29%, 50%);background-color:var(--primary-color);font-size:0.2rem;padding:10px;border:0}.header-group .clearBtn{font-size:1rem;color:#000;background-color:transparent;border:0;cursor:pointer;font-weight:500}@media screen and (max-width: 400px){.header-group .clearBtn{font-size:1.2rem}}.header-group .clearBtn:hover{color:hsl(180, 29%, 50%);color:var(--primary-color);text-decoration:underline}.job{display:flex;flex-direction:row;background-color:#fff;grid-column-gap:25px;-webkit-column-gap:25px;column-gap:25px;margin-top:3.5rem;padding:1.4rem;align-items:center;box-shadow:rgba(91,164,164,0.35) 0px 5px 15px;overflow:hidden}.job .company{grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px}@media screen and (max-width: 860px){.job{flex-direction:column;align-items:flex-start;grid-row-gap:10px;row-gap:10px;position:relative;overflow:visible}.job .company{grid-column-gap:8px;-webkit-column-gap:8px;column-gap:8px}.job .logo{position:absolute;top:-1.8rem}.job .logo img{min-width:50px;width:10%}}.job .details{grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px}.job .company,.job .details{font-size:0.8rem;display:flex;margin:0.7rem 0;align-items:center}@media screen and (max-width: 400px){.job .company,.job .details{font-size:0.7rem}}.job .company .cname,.job .details .cname{color:hsl(180, 29%, 50%);color:var(--primary-color)}.job .company .new,.job .details .new{background-color:hsl(180, 29%, 50%);background-color:var(--primary-color);color:white;padding:5px 7px;border-radius:4px;text-transform:uppercase}.job .company .featured,.job .details .featured{background-color:hsl(180, 8%, 52%);background-color:var(--dark-greyish);padding:5px 7px;border-radius:5px;color:white;text-transform:uppercase}.job .position{color:hsl(180, 29%, 50%);color:var(--primary-color);font-weight:700}.job .part2{display:flex;grid-column-gap:7px;-webkit-column-gap:7px;column-gap:7px;flex-wrap:wrap;grid-row-gap:8px;row-gap:8px}.job .part1{flex-grow:1}button.button,ul li{background-color:hsl(180, 52%, 96%);background-color:var(--background-color);color:hsl(180, 29%, 50%);color:var(--primary-color);border:0;padding:8px 12px;cursor:pointer;border-radius:5px}button.button:hover,ul li:hover{color:#fff;background-color:hsl(180, 29%, 50%);background-color:var(--primary-color)}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

